import React, { useState, useEffect } from 'react';
import './css/Footer.css';

function Footer() {
  const [animationOverd, setAnimationOverd] = useState(false);

  const footerEvent = () => {
    const promise = new Promise((resolve) => {
      setTimeout(() => {
        resolve(document.getElementById('footerImage1').classList.add('active'));
      },1000)
    })
    promise.then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(document.getElementById('footerImage2').classList.add('active'));
        },1000)
      })
    }).then((resolve) => {
      setTimeout(() => {
        document.getElementById('footerImage3').classList.add('active');
        setAnimationOverd(true);
      },1000)
    })
  }

  const callFooterEvent = () => {
    if(!animationOverd){
      footerEvent();
    }
  }

  useEffect(() => {
    window.addEventListener('load',callFooterEvent);
    return(() => {
      window.removeEventListener('load',callFooterEvent);
    })
  },[])

  return(
    <>
      <div className="footer">
        <div className="footer-images">
          <img src={`${process.env.PUBLIC_URL}/img/footer-city1.png`} alt="" className="footer-image" id="footerImage1" />
          <img src={`${process.env.PUBLIC_URL}/img/footer-city2.png`} alt="" className="footer-image" id="footerImage2" />
          <img src={`${process.env.PUBLIC_URL}/img/footer-city3.png`} alt="" className="footer-image" id="footerImage3" />
        </div>
        <div className="footer-text">
          <h1>info</h1>
        </div>
      </div>
    </>
  )
}

export default Footer;